import "./consiglio.js";

$(document).ready(function () {

    var urlParams = new URLSearchParams(window.location.search);

    console.log(urlParams.get('sub_menu_id'));

    var type_id = urlParams.get('ref_type_id');
    var section_id = urlParams.get('section_id');
    if (urlParams.has('sub_menu_id') && urlParams.get('sub_menu_id') != null) {
        let menu_url_to_call = apibase.baseurl + "tree/1003/" + urlParams.get('sub_menu_id');
        $.ajax({
            url: menu_url_to_call, success: function (result) {
                if (result != null && result.children != undefined) {
                    console.log('submenu', result);
                    let activePage = "";
                    let megaMenuLinks = result.children;

                    let megaMenuHtml = '<h1 class="head_side_menu title_menu_right" id="heading">' + result.title.toUpperCase() + '</h1><ul class="link-list">';
                    var hideSubMenu = true;
                    let title_discovered = false;

                    for (var k = 0; k < megaMenuLinks.length; k++) {

                        console.log(megaMenuLinks[k]);
                        let element = megaMenuLinks[k];
                        let ref_type = element.properties.ref_typeid;
                        localStorage.setItem(element.id + "label", element.title);

                        let link = "./pagina_base.html?sub_menu_id=" + result.id;
                        let active = "";
                        if (ref_type != "") {
                            let config = Consiglio.getConfigurazione(ref_type);
                            let query = "?ref_type_id=" + ref_type + "&" + "sub_menu_id=" + result.id;
                            let template = config.template;
                            if (redirectableOtype.includes(ref_type)) {
                                template = "redirect.html";
                            }
                            link = template + query;
                        }

                        link += "&section_id=" + element.id;
                        if (section_id === element.id + "") {
                            active = "active";
                            if (urlParams.has('filtro')) {
                                active = "";
                                var ref_id = urlParams.get('ref_type_id');
                                var config = Consiglio.getConfigurazione(ref_id).getSottovista(urlParams.get('filtro'));
                                if (config.label) {
                                    $("#title").html(config.label);
                                    title_discovered = true;
                                }
                            } else if (!urlParams.has('detail_obj_id')) {
                                $("#title").text(element.title);
                                title_discovered = true;
                            }

                            activePage = element.title;
                        }
                        if (urlParams.has('leg')) {
                            link += "&leg=" + urlParams.get('leg');
                        }

                        if (element.properties.url != undefined) {
                            if (element.properties.url != "") {
                                link = element.properties.url;
                            }
                        }

                        if (urlParams.has("prev_page_id")) {
                            link += "&prev_page_id=" + urlParams.get("prev_page_id");
                        }
                        if (urlParams.has("prev_sub_menu")) {
                            link += "&prev_sub_menu=" + urlParams.get('prev_sub_menu');
                        }


                        if (active === "active" && element.children.length > 0) {
                            let megaMenuHtmlSub = '<ul class="bg-dark-blue" style="padding-right: 1em;">';
                            for (var o = 0; o < element.children.length; o++) {
                                let subEl = element.children[o];
                                localStorage.setItem(subEl.id + "label", subEl.title);

                                let link = "./pagina_base.html?sub_menu_id=" + element.id + "&detail_sub_menu=" + subEl.id;
                                let active = "";
                                let ref_type_sub = subEl.properties.ref_typeid;
                                if (ref_type_sub != "") {
                                    let config = Consiglio.getConfigurazione(ref_type_sub);
                                    let query = "?ref_type_id=" + ref_type_sub + "&" + "sub_menu_id=" + element.id + "&detail_sub_menu=" + subEl.id;
                                    let template = config.template;
                                    if (redirectableOtype.includes(ref_type_sub)) {
                                        template = "redirect.html";
                                    }
                                    link = template + query;
                                }
                                let target = "_self";
                                link += "&section_id=" + subEl.id;
                                link += "&prev_page_id=" + element.id;
                                link += "&prev_sub_menu=" + urlParams.get('sub_menu_id');

                                if (urlParams.has("prev_page_id")) {
                                    link += "&prev_prev_page_id=" + urlParams.get("prev_page_id");
                                }
                                if (urlParams.has("prev_sub_menu")) {
                                    link += "&prev_prev_sub_menu=" + urlParams.get('prev_sub_menu');
                                }

                                if (subEl.properties.url != undefined) {
                                    if (subEl.properties.url != "") {
                                        link = subEl.properties.url;
                                        target = "_self";
                                    }
                                }


                                megaMenuHtmlSub += '<li class="side_menu"><div class="div-menu"><a class="list-item-a" href="' + link + '" target="' + target + '"><span>' + subEl.title + ' </span> </a></div></li>';
                                $("#buttonSubItem").text(element.title.toUpperCase());

                                hideSubMenu = false;
                            }
                            megaMenuHtmlSub += "</ul>";
                            $("#accordionSubItem").html(megaMenuHtmlSub);
                            $("#buttonSubItem").text(element.title.toUpperCase());
                        }


                        megaMenuHtml += '<li class="side_menu" ><div class="div-menu"><a class="list-item-a ' + active + '" href="' + link + '"><span>' + element.title + '</span></a></div>';
                        megaMenuHtml += "</li>";
                        if (urlParams.has('prev_page_id')) {
                            if (element.id + "" === urlParams.get('prev_page_id')) {
                                for (var o = 0; o < element.children.length; o++) {
                                    let subEl = element.children[o];
                                    if (subEl.id + "" === section_id) {
                                        let megaMenuHtmlSub = '<ul class="bg-dark-blue" style="padding-right: 1em;">';
                                        for (var oo = 0; oo < subEl.children.length; oo++) {
                                            let subEll = subEl.children[oo];
                                            localStorage.setItem(subEll.id + "label", subEll.title);
                                            let link = "./pagina_base.html?sub_menu_id=" + subEl.id + "&detail_sub_menu=" + subEll.id;
                                            let active = "";
                                            let ref_type_sub = subEl.properties.ref_typeid;
                                            if (ref_type_sub != "") {
                                                let config = Consiglio.getConfigurazione(ref_type_sub);
                                                let query = "?ref_type_id=" + ref_type + "&" + "sub_menu_id=" + urlParams.get('sub_menu_id') + "&detail_sub_menu=" + subEll.id;
                                                let template = config.template;
                                                if (redirectableOtype.includes(ref_type_sub)) {
                                                    template = "redirect.html";
                                                }
                                                link = template + query;
                                            }
                                            let target = "_self";
                                            link += "&section_id=" + subEll.id;
                                            link += "&prev_page_id=" + subEl.id;
                                            link += "&prev_sub_menu=" + element.id;

                                            if (urlParams.has('prev_sub_menu')) {
                                                link += "&prev_prev_sub_menu=" + urlParams.get('sub_menu_id');
                                            }
                                            if (subEl.properties.url != undefined) {
                                                if (subEl.properties.url != "") {
                                                    link = subEll.properties.url;
                                                    target = "_self";
                                                }
                                            }


                                            megaMenuHtmlSub += '<li class="side_menu"><div class="div-menu"> <a class="list-item-a" href="' + link + '" target="' + target + '"><span>' + subEll.title + '  </span></a></div></li>';
                                            $("#buttonSubItem").text(subEl.title.toUpperCase());

                                            hideSubMenu = false;
                                        }
                                        megaMenuHtmlSub += "</ul>";
                                        $("#accordionSubItem").html(megaMenuHtmlSub);
                                    }
                                }
                            }
                        }
                    }




                    if (!title_discovered) {
                        var title = $("#title").text();
                        if (title === "") {
                            $("#title").text(otypeLabels[type_id]);
                        }
                    }
                    megaMenuHtml += "</ul>";
                    if (hideSubMenu) {
                        $("#accordionSubItem").hide();
                        $("#buttonSubItem").hide();
                    } else {
                        $("#buttonSubItem").click();
                    }
                    $("#side_menu").append(megaMenuHtml);
                }
            }
        });
    } else {
        $("#side_menu").hide();
        $("#title").text(otypeLabels[type_id]);

        $("#container_base").attr("class", "col-12");
        $("#fascia_elenco").attr("class", "col-12");

    }


    if (otypeConArchivio.includes(urlParams.get('ref_type_id'))) {

        let leg_url = apibase.baseurl + "10000";
        $.ajax({
            url: leg_url, success: function (legresult) {
                if (legresult != null) {
                    let megaMenuLinks = legresult.content;
                    let megaMenuHtml = "";

                    let baseLink = "./pagina_elenco.html" + "?ref_type_id=" + type_id + "&" + "sub_menu_id=" + urlParams.get('sub_menu_id');
                    if (redirectableOtype.includes(type_id)) {
                        baseLink = "./redirect.html?ref_type_id=" + type_id + "&" + "sub_menu_id=" + urlParams.get('sub_menu_id');
                    }

                    if (urlParams.has('leg')) {
                        $("#title_prev").html('<a class="list-item " href="' + baseLink + '"><span>Torna alla legislatura ' + currentLeg + '</span></a>');
                    }
                    megaMenuHtml += '<ul class="link-list "> ';
                    for (var k = 0; k < megaMenuLinks.length; k++) {
                        console.log(megaMenuLinks[k]);
                        let element = megaMenuLinks[k];
                        var link = "./pagina_elenco.html" + "?ref_type_id=" + type_id + "&" + "sub_menu_id=" + urlParams.get('sub_menu_id') + "&leg=" + element.title;
                        if (redirectableOtype.includes(type_id)) {
                            link = "./redirect.html?ref_type_id=" + type_id + "&" + "sub_menu_id=" + urlParams.get('sub_menu_id') + "&leg=" + element.title;;
                        }
                        if (urlParams.has('section_id')) {
                            link += "&section_id=" + urlParams.get('section_id');
                        }
                        let active = "";
                        if (element.title === urlParams.get('leg')) {
                            active = "active";
                        }
                        megaMenuHtml += '<li class="side_menu"><a class="list-item  ' + active + '" href="' + link + '"><span>' + element.title + '</span></a></li>';
                    }
                    megaMenuHtml += "</ul>";

                    $("#accordionLeg").html(megaMenuHtml);
                    $("#buttonLegs").text("Legislature precedenti");
                }

            }
        });

    } else {
        $("#accordionLeg").hide();
        $("#buttonLegs").hide();
    }

});