
import { Utils } from "../../commons/js/utils.js";
import { News, newsComponent } from "../../commons/js/news.js";

export const NotizieCorrelate = {
    render: function (correlabile) {
        return new Promise(function (resolve, reject) {
            $.ajax({
                url: apibase.baseurl + '/10500' + '?size=3',
                method: 'POST',
                data: JSON.stringify({
                    argomento: correlabile.properties.argomento,
                    "SORT": [{
                        "direction": "DESC",
                        "property": "data_pubblicazione"
                    },{
                        "direction": "DESC",
                        "property": "data_pubblicazione_time"
                    }]
                }),
                contentType: 'application/json',
                dataType: 'json',
                success: function (result) {
                    if (result != null && result.content.length > 0) {
                        var allNews = result.content;
                        var newsHtml = [];
                        allNews.forEach(function (item) {
                            if (item.id != correlabile.id && newsHtml < 2) {
                                let news = new News();
                                if (item.properties.url) {
                                    news.link = {
                                        url: item.properties.url,
                                        target: '_blank'
                                    }
                                } else {
                                    news.link = {
                                        url: 'notizia.html?id=' + item.id,
                                        target: '_self'
                                    }
                                }
                                news.dataPubblicazione = item.properties.data_pubblicazione;
                                news.oraPubblicazione = item.properties.data_pubblicazione_time.slice(0, 5);
                                news.estratto = item.properties.intestazione;
                                news.titolo = item.properties.title;
                                if (item.components.immagine && item.components.immagine.components.length) {
                                    news.immagine = {
                                        titolo: item.components.immagine.components[0].title,
                                        src: baseImgUrl + item.components.immagine.components[0].components.file.components[0].properties.address
                                    }
                                    news.immagine.src = Utils.creaPlaceholder(news.immagine.src, '//via.placeholder.com/512x320');
                                }
                                newsHtml.push(newsComponent.renderNews(news));
                            }
                        });
                        resolve(newsHtml.join(''));
                    }
                },
                error: reject
            })
        });
    }
}
