import "./consiglio.js";
import "../../commons/js/TreeModel.js";

$(document).ready(function () {
    var tree = new TreeModel();
    var retrivedTree = JSON.parse(localStorage.getItem("megaMenu"));
    let root = tree.parse(retrivedTree);


    var urlParams = new URLSearchParams(window.location.search);

    var breadCrump = '<li class="breadcrumb-item"><a href="index.html">Home</a><span class="separator">/</span> </li>';

    if (urlParams.has('section_id')) {
        var section_id = urlParams.get('section_id')
        var node12 = root.first(function (node) {
            return node.model.id === Number(section_id);
        });
        var path = node12.getPath();
        for (var i = 2; i < path.length; i++) {
            var el_pre = path[i - 1];
            var el = path[i];
            let ref_type = el.model.properties.ref_typeid;
            let query = "?sub_menu_id=" + el_pre.model.id;
            let template = "pagina_base.html";
            if (ref_type != "") {
                let config = Consiglio.getConfigurazione(ref_type);
                template = config.template;
                query += "&ref_type_id=" + ref_type;
                if (redirectableOtype.includes(ref_type)) {
                    template = "redirect.html";
                }
            }
            let link = template + query;
            link += "&section_id=" + el.model.id;
            localStorage.setItem(el.model.id + "label", el.model.title);
            if (el.model.properties.url != undefined) {
                if (el.model.properties.url != "") {
                    link = el.model.properties.url;
                }
            }
            if (path.length - 1 == i && !urlParams.get('filtro')) {
                breadCrump += '<li class="breadcrumb-item" aria-current="page">' + el.model.title + '</li> ';
            } else {
                breadCrump += '<li class="breadcrumb-item" aria-current="page"><a href="' + link + '">' + el.model.title + '</a><span class="separator">/</span></li> ';
            }
        }
        if (urlParams.get('filtro')) {
            var ref_id = urlParams.get('ref_type_id');
            var config = Consiglio.getConfigurazione(ref_id).getSottovista(urlParams.get('filtro'));
            if (config.label) {
                breadCrump += '<li class="breadcrumb-item" aria-current="page">' + config.label + '</li> ';
            }
        }


    }
    $("#bread").html(breadCrump);





});