export const Argomento = function () {
    this.nome;
    this.peso;
    this.icona;
    this.id;
}

var VistaArgomenti = function () {
    this.minFont = 12;
    this.maxFont = 20;
    this.minWeight = 300;
    this.maxWeight = 600;
};

VistaArgomenti.prototype.renderArgomenti = function (argomenti) {
    let html = '';
    argomenti.forEach((argomento) => {
        const fontSize = ((this.maxFont - this.minFont) * argomento.peso) + this.minFont;
        const fontWeight = ((this.maxWeight - this.minWeight) * argomento.peso) + this.minWeight;
        const style = 'font-size: ' + fontSize + 'px;font-weight: ' + fontWeight;
        const link = 'notizie.html?argomento=' + argomento.id;
        html += '\
        <div class="col-4 p-2">\
            <div class="text-center">\
                <a href="' + link + '"><svg class="icon icona-argomento"><use xlink:href="' + argomento.icona + '"></use></svg></a>\
            </div>\
            <div class="text-center">\
                <a class="dark-obj-name" style="' + style + '" href="' + link + '">' + argomento.nome + '</a>\
            </div>\
        </div>'
    });
    return html;
};

export const vistaArgomenti = new VistaArgomenti();

