import "../../commons/js/conf.js";
import { MegaMenu } from "../../commons/js/mega-menu.js";
import { ConsiglioInforma } from "../js/consiglioinforma.js";

function creaLink(item, parent) {
    let ref_type = item.properties.ref_typeid;
    let template = './pagina_base.html';
    let query = '?ref_type_id=' + ref_type + '&section_id=' + item.id;
    if (parent) {
        query += "&sub_menu_id=" + parent.id;
    }
    var target = "_self";
    if (ref_type != "") {
        template = ConsiglioInforma.getConfigurazione(ref_type).template;
    }
    var url = template + query;
    if (item.properties.url != undefined) {
        if (item.properties.url != "") {
            url = item.properties.url;
            target = "_self";
        }
    }
    return { url, target };
}

export default {
    name: "Header",
    data: function () {
        let megaMenu = new MegaMenu(creaLink);
        let menu_url_to_call = apibase.baseurl + "tree/1003/204928";
        $.ajax({
            url: menu_url_to_call, success: function (result) {
                const mobileMenu = megaMenu.renderMobile(result);
                $("#collapseOne").html(mobileMenu);
                $("#megaMenu").html(megaMenu.render(result));
                $(".dropdown-toggle").dropdown();
            }
        });
        return {
            baseUrl: process.env.BASE_URL
        };
    }
};