import "./consiglio.js";
import "../../commons/js/TreeModel.js";

$(document).ready(function () {
    var urlParams = new URLSearchParams(window.location.search);

    var breadCrump = '<li class="breadcrumb-item"><a href="index.html">Home</a><span class="separator">/</span> </li>';
    let linkBR = "";

    var subsection = subSectionsFor[urlParams.get('ref_type_id')];
    if(subsection != undefined) {
        var linkSub = "";
        if(subsection.sub_menu != undefined) {
            linkSub = "./pagina_elenco.html?ref_type_id="+subsection.type+"&"+"sub_menu_id="+subsection.sub_menu;
        }else {
            linkSub = "./pagina_elenco.html?ref_type_id="+subsection.type;
        }
        if(redirectableOtype.includes(subsection.type+"")){
            linkSub = "./redirect.html?ref_type_id=" + subsection.type;
            if(subsection.sub_menu != undefined) {
                linkSub +="&sub_menu_id="+subsection.sub_menu;
            }
        }
        if(urlParams.has('leg')) {
            linkSub += "&leg="+urlParams.get('leg');
        }

        breadCrump +=  '<li class="breadcrumb-item" aria-current="page"><a href="'+linkSub+'">'+ otypeLabels[subsection.type]+'</a><span class="separator">/</span></li> ';
    }





    if(urlParams.has("detail_obj_id")) {

        if(urlParams.has("sub_menu_id")) {
            linkBR = "./pagina_elenco.html?ref_type_id="+urlParams.get('ref_type_id')+"&"+"sub_menu_id="+urlParams.get('sub_menu_id');
        }else {
            linkBR = "./pagina_elenco.html?ref_type_id="+urlParams.get('ref_type_id');
        }


        if(redirectableOtype.includes(urlParams.get('ref_type_id'))){
            linkBR = "./redirect.html?ref_type_id=" + urlParams.get('ref_type_id');
            if(urlParams.has('sub_menu_id')) {
                linkBR +="&sub_menu_id="+urlParams.get('sub_menu_id');
            }
        }


        if(urlParams.has('leg')) {
            linkBR += "&leg="+urlParams.get('leg');
        }
        if(redirectableOtype.includes(urlParams.get('ref_type_id'))){
            linkBR = "#";
        }
        breadCrump +=  '<li class="breadcrumb-item" aria-current="page"><a href="'+linkBR+'">'+otypeLabels[urlParams.get('ref_type_id')]+'</a><span class="separator"></span> </li> ';
        $("#bread").html(breadCrump);

    }

    else {
        if(urlParams.has('prev_prev_page_id')) {
            var label = "";
            var linkSub = "pagina_base.html?section_id="+urlParams.get('prev_prev_page_id');

            if(urlParams.has('prev_prev_sub_menu')) {
                linkSub += "&sub_menu_id="+urlParams.get('prev_prev_sub_menu');
            }
            var sectionId = urlParams.get('prev_prev_page_id')+"label";
            var retrievedObject = localStorage.getItem(sectionId);
            if(retrievedObject != undefined) {
                label = retrievedObject;
            }else   {
                label = sectionId;
            }

            if(urlParams.has('prev_page_id')) {
                linkSub += "&prev_page_id="+urlParams.get('prev_page_id');
            }


            if(urlParams.has('prev_sub_menu')) {
                linkSub += "&prev_sub_menu="+urlParams.get('prev_sub_menu');
            }


            breadCrump +=  '<li class="breadcrumb-item" aria-current="page"><a href="'+linkSub+'">'+label+'</a><span class="separator">/</span></li>';
        }

        if(urlParams.has('prev_page_id')) {
            var label = "";
            var linkSub = "pagina_base.html?section_id="+urlParams.get('prev_page_id');

            if(urlParams.has('prev_sub_menu')) {
                linkSub += "&sub_menu_id="+urlParams.get('prev_sub_menu');
            }
            var sectionId = urlParams.get('prev_page_id')+"label";
            var retrievedObject = localStorage.getItem(sectionId);
            if(retrievedObject != undefined) {
                label = retrievedObject;
            }else   {
                label = sectionId;
            }

            if(urlParams.has('prev_prev_page_id')) {
                linkSub += "&prev_prev_page_id="+urlParams.get('prev_prev_page_id');
            }
            if(urlParams.has('prev_prev_sub_menu')) {
                linkSub += "&prev_prev_sub_menu="+urlParams.get('prev_prev_sub_menu');
            }


            breadCrump +=  '<li class="breadcrumb-item" aria-current="page"><a href="'+linkSub+'">'+label+'</a></li>';
        }else {
            let label = otypeLabels[urlParams.get('ref_type_id')];
            if(label == undefined) {
                var sectionId = urlParams.get('section_id')+"label";
                var retrievedObject = localStorage.getItem(sectionId);
                if(retrievedObject != undefined) {
                    label = retrievedObject;
                }else   {
                    label = sectionId;
                }

                if(label === "nulllabel") {
                    label = "Ricerca Contenuti"
                }
            }
            breadCrump +=  '<li class="breadcrumb-item active" aria-current="page"><a href="#">'+label+'</a>';
        }


        $("#bread").html(breadCrump);
    }




});