export const Paginazione = {

    inizializzaUrl: function (urlParams) {
        if (urlParams.has('page')) {
            return "&page=" + urlParams.get('page');
        }
        return '';
    },

    renderizzaComponente: function (result, stileCustom) {
        const stile = Object.assign({
            paginationWrapper: ''
        }, stileCustom);
        if (result.totalPages > 1) {
            var disabled_prev = '';
            var disabled_next = '';
            if (result.first) {
                disabled_prev = 'disabled';
            }
            if (result.last) {
                disabled_next = 'disabled';
            }
            var actual_page = result.number;
            var next_page = actual_page + 1;
            var prev_page = actual_page - 1;
            var actual_location = window.location.search.length ? window.location.href.replace(/&?page=[^&]*/, ''): window.location.href + '?';
            var prevLink = actual_location + '&page=' + prev_page;
            var nextLink = actual_location + '&page=' + next_page;
            let paginationHtml =
                '<nav class="pagination-wrapper ' + stile.paginationWrapper + '" aria-label="Paginazione">\
                    <ul class="pagination">\
                        <li class="page-item ' + disabled_prev + '">\
                            <a class="page-link" href=' + prevLink + ' tabindex="-1" aria-hidden="true">\
                                <svg class="icon icon-primary"><use xlink:href="' + process.env.BASE_URL + 'svg/sprite.svg#it-chevron-left"></use></svg>\n\
                                <span class="sr-only">Pagina precedente</span>\n\
                            </a>\n\
                        </li>\n\
                        <li class="page-item"><span class="page-link">' + (actual_page + 1) + '</span></li>\
                        <li class="page-item"><span class="page-link">/</span></li>\
                        <li class="page-item"><span class="page-link">' + result.totalPages + '</span></li>\
                        <li class="page-item sr-only">\
                          <a class="page-link" href="#" aria-current="page">Pagina '+ (actual_page + 1) + ' di ' + result.totalPages + '</a >\
                        </li >\
                        <li class="page-item ' + disabled_next + '">\n\
                            <a class="page-link" href=' + nextLink + ' tabindex="-1" aria-hidden="true">\n\
                                <span class="sr-only">Pagina successiva</span>\n\
                                <svg class="icon icon-primary"><use xlink: href="' + process.env.BASE_URL + 'svg/sprite.svg#it-chevron-right"></use></svg>\n\
                            </a>\n\
                        </li >\n\
                  </ul >\n\
                </nav > '
            return paginationHtml;
        }
    }
};