import "./consiglio.js";
import { Pagina } from "../../commons/js/pagina.js";

$(document).ready(function () {
    var urlParams = new URLSearchParams(window.location.search);
    if (urlParams.has('section_id')) {
        let section_id = urlParams.get('section_id');
        var url_to_call = apibase.baseurl + "pagina/" + section_id;
        $.ajax({
            url: url_to_call, success: function (result_api) {
                $("#title").text(result_api.title);
                $("#container_base").append(Pagina.renderPaginaBase(result_api));
            }, error: function (res_err) {
                var label = "";
                var sectionId = urlParams.get('section_id') + "label";
                var retrievedObject = localStorage.getItem(sectionId);
                if (retrievedObject != undefined) {
                    label = retrievedObject;
                } else {
                    label = sectionId;
                }
                $("#title").text(label);

            }
        });

    }
})