import { StringRenderer } from "./string_renderer.js";

var NewsComponent = function () {
};


NewsComponent.prototype.renderNews = function (news) {
    var data = StringRenderer.renderDataTestuale(news.dataPubblicazione)
        + ', ' + news.oraPubblicazione;
    var corpoNews = '';
    if (news.immagine) {
        corpoNews = '<p class="mt-2 ml-2"><img width="512" height="320" src="' + news.immagine.src + '" alt="' + news.immagine.titolo + '" class="img-fluid" ></p>';
    } else if (news.estratto) {
        corpoNews = '<p class="mt-2 ml-2">' + news.estratto + '</p>';
    }
    return '\
        <div>\
            <p class="mt-2 ml-2 mb-0 ">\
                <svg class="icon icon-dark"> <use xlink:href="' + process.env.BASE_URL + 'svg/ba-sprite.svg#ba-news"></use></svg>\
                <span class="data ml-1">' + data + '</span>\
            </p>\
            <div class="h6 ml-3">\
                <a class="black-obj-name-bold" target="' + news.link.target + '" href="' + news.link.url + '" >' + news.titolo + '</a>\
            </div>\
            ' + corpoNews + '\
        </div>';

}

NewsComponent.prototype.renderNewsCompatta = function (news) {
    var data = StringRenderer.renderDataTestuale(news.dataPubblicazione);
    return '\
        <div class="container-fluid pb-1">\
            <div class="row">\
                <div class="col-4 light-obj-name-bold text-right">' + data + '<span class="no-wrap"> ore ' + news.oraPubblicazione + '</span></div>\
                <div class="col-8" >\
                    <a class="black-obj-name-bold" target="' + news.link.target + '" href="' + news.link.url + '" >' + news.titolo + '</a>\
                </div>\
            </div>\
        </div>';

}

NewsComponent.prototype.renderNewsMultiRiga = function (news) {
    var data = StringRenderer.renderDataTestuale(news.dataPubblicazione);
    return '\
        <div class="container-fluid pb-3">\
            <div class="row">\
                <div class="col-5 light-obj-name-bold" >\
                    <div class="text-right">' + data + '</div>\
                    <div class="text-right"> ore ' + news.oraPubblicazione + '</div>\
                </div>\
                <div class="col-7" >\
                    <a class="black-obj-name-bold" target="' + news.link.target + '" href="' + news.link.url + '" >' + news.titolo + '</a>\
                </div>\
            </div>\
        </div>';

}

export const News = function () {
    this.dataPubblicazione = null;
    this.oraPubblicazione = null;
    this.titolo = null;
    this.estratto = null;
    this.immagine = null;
    this.link = null;
};

export const newsComponent = new NewsComponent();
