export const AnteprimaGalleria = function () {
    this.srcImmagine = undefined;
    this.titoloImmagine = undefined;
    this.linkOggetto = undefined;
    this.titoloOggetto = undefined;
    this.linkArchivio = undefined;
}

export const AnteprimaImmagine = function () {
    this.src = undefined;
    this.thumbnail = undefined;
    this.titolo = undefined;
    this.idGalleria = 'galleria';
}

export const VistaGallerie = {
    renderAnteprimaGalleria: function (galleria) {
        let html = '<div class="mb-2 media">\
                        <a href="' + galleria.linkOggetto + '"><img width="160" height="90" class="ci-thumb thumb-fit mr-2" src="' + galleria.srcImmagine + '" alt="' + galleria.titoloImmagine + '"></a>\
                        <div class="media-body"><a class="dark-obj-name-bold" href="' + galleria.linkOggetto + '">' + galleria.titoloOggetto + '</a></div>\
                    </div>';
        if (galleria.linkArchivio) {
            html += '<div class="p-4"><a class="btn bottone-con-freccia bottom-right text-uppercase" href="' + galleria.linkArchivio + '">Vedi tutte</a></div>';
        }
        return html;
    },
    renderAnteprimaImmagine: function (immagine) {
        return '<div class="mb-2 media">\
                    <a href="' + immagine.src + '" target="_self" class="dark-obj-name-bold" data-caption="' + immagine.titolo + '" data-fancybox="' + immagine.idGalleria + '">\
                        <img width="160" height="90" class="ci-thumb thumb-fit mr-2" src="' + immagine.thumbnail + '" alt="' + immagine.titolo + '">\
                    </a>\
                </div>';
    }
}