import { StringRenderer } from "./string_renderer.js";
import { Utils } from "./utils.js";

export const Pagina = {
    renderIntestazione: function (result) {
        let intestazione = result.properties.intestazione;
        if (intestazione == undefined) {
            intestazione = "";
        }
        return '<p>' + intestazione + '</p> <p><i> ' + this.renderDataAggiornamento(result) + '</i></p>';
    },
    renderPieDiPagina: function (result) {
        var stringedDate = this.renderDataPubblicazione(result);
        return '<p>' + stringedDate + '</p>';
    },
    renderImmagine: function (result) {
        if (result.components.immagine != undefined) {
            if (result.components.immagine.components.length > 0) {
                let righeImmagini = '';
                result.components.immagine.components.forEach(function (cartella) {
                    var iconLink, thumb;
                    if (cartella.properties.address != undefined) {
                        iconLink = baseImgUrl + cartella.properties.address;
                        thumb = baseImgUrl + cartella.properties.thumbadress;
                        iconLink = Utils.creaPlaceholder(iconLink, '//via.placeholder.com/1280x768');
                        thumb = Utils.creaPlaceholder(thumb, '//via.placeholder.com/512x320');
                        var content = '<img class="img-fluid" src="' + iconLink + '" data-src="' + thumb + '" alt="' + cartella.title + '" title="' + cartella.title + '" /> <hr  class="border-news mb-0" />';
                        righeImmagini += content;
                    }
                });
                return '\
                    <div class="row">\
                        <div class="container " id = "row_immagine" > '+ righeImmagini + '</div>\
                    </div > ';
            }
        }
        return '';
    },
    renderCartelle: function (pagina) {
        if (pagina.components.cartella && pagina.components.cartella.components.length) {
            let righeCartelle = '';
            pagina.components.cartella.components.forEach(function (cartella) {
                var content = "<h6 class=\"dark-obj-name\">" + cartella.title + "</h6>";
                let collaboratori = cartella.components.attachment;
                collaboratori.components.forEach(function (element) {
                    content += '<div class="row p-2 bg-title-area m-2"  ><div class="col"> <a href="' + baseImgUrl + element.properties.address + '" target="_blank"> ' + element.title + ' </a></div></div>';
                });
                let links = cartella.components.link;
                links.components.forEach(function (element) {
                    content += '<div class="row p-2 bg-title-area m-2"  ><div class="col"> <a href="' + element.properties.url + '" target="_blank"> ' + element.title + ' </a></div></div>';
                });
                righeCartelle += content;
            });
            return '<div class="container-fluid " id="row_cartelle">' + righeCartelle + '</div>';
        }
        return '';
    },
    renderAllegati: function (pagina) {
        if (pagina.components.allegato && pagina.components.allegato.components.length) {
            let righeAllegati = '';
            pagina.components.allegato.components.forEach(function (allegato) {
                righeAllegati += '\
                <div class="row  bg-title-area m-2">\
                    <div class="col">\
                        <a href="' + baseImgUrl + allegato.properties.address + '" target="_blank"> ' + allegato.title + ' </a>\
                    </div>\
                </div>';
            });
            let allegatiHtml = '\
                <hr class="border-news mb-0"/>\
                <h6 class="dark-obj-name ml-2 mt-2">' + pagina.components.allegato.label + '</h6>\
                <div class="container-fluid mb-4 " id="row_allegato">' + righeAllegati + '</div>'
            return allegatiHtml;
        }
        return '';
    },
    renderLink: function (pagina) {
        if (pagina.components.link != undefined && pagina.components.link.components.length > 0) {
            let righeLink = '';
            pagina.components.link.components.forEach(function (cartella) {
                righeLink += '\
                    <div class="row m-2">\
                        <div class="col">\
                            <a href="' + cartella.properties.url + '" target="_blank"> ' + cartella.title + ' </a>\
                        </div>\
                    </div>';
            });
            return '\
            <hr class="border-news mb-0"/>\
            <h6 class="dark-obj-name">' + pagina.components.link.label + '</h6>\
            <div class="container " id="row_links">'+ righeLink + '</div>';
        }
        return '';
    },
    renderDataPubblicazione(result) {
        var stringedDate = '';
        if (result.properties.data_inserimento != undefined) {
            stringedDate = StringRenderer.renderDataTestuale(result.properties.data_inserimento);
        } else if (result.creationDate != undefined) {
            stringedDate = StringRenderer.renderDataTestuale(result.creationDate);
        }
        if (stringedDate.length) {
            return '<i>Pubblicata il ' + stringedDate + '</i>';
        }
        return '';
    },
    renderDataAggiornamento(result) {
        var stringedDatePub = "";
        if (result.properties.data_aggiornamento != undefined) {
            stringedDatePub = StringRenderer.renderDataTestuale(result.properties.data_aggiornamento);
        } else if (result.lastModifiedDate) {
            stringedDatePub = StringRenderer.renderDataTestuale(result.lastModifiedDate);
        }
        if (stringedDatePub.length) {
            return '<i>Ultimo aggiornamento ' + stringedDatePub + '</i>';
        }
        return '';
    },
    renderPaginaBase(result) {
        return this.renderIntestazione(result) +
            this.renderImmagine(result) +
            (result.properties.descrizione || result.properties.testo || result.properties.corpo || '') +
            this.renderCartelle(result) +
            this.renderAllegati(result) +
            this.renderLink(result) +
            this.renderPieDiPagina(result);
    }

}
