import "./consiglio.js";

//$(document).ready(function() {

    var url_to_call = apibase.baseurl + site_config.siteID + "/" + site_config.typeID_header_footer;
    $.ajax({url: url_to_call, cache: false, success: function(result){
            console.log('result api call: ',result);
            if(result.components != undefined){
                if(result.components.link_social != undefined) {
                    let socialLinks = result.components.link_social.components;
                    console.log('social link found',socialLinks);
                    if(socialLinks.length > 0 ){
                        var socialHtml = "<span>Seguici su</span><ul>";
                        var socialFooter = "<h6>Seguici su</h6><hr class='bg-white'/> <ul class=\"list-inline text-left social\">\n\n"
                        var socialFooterXs = "<span>Seguici su</span><hr class='bg-white'/> <ul class=\"list-inline text-left social\">\n\n"
                        socialLinks.forEach(function (element) {
                            socialHtml += '<li><a href="'+ element.properties.url+'"aria-label="'+element.title+'" target="_blank"><svg class="icon"><use xlink:href="svg/sprite.svg#'+element.properties.icon+'"></use></svg></a></li>';
                            socialFooter += '<li class="list-inline-item"><a class="p-2 text-white" href="'+ element.properties.url+'" target="_blank"><svg class="icon icon-sm icon-white align-top"><use xlink:href="svg/sprite.svg#'+element.properties.icon+'"></use></svg><span class="sr-only">'+element.title+'</span></a></li>';
                            socialFooterXs += '<li class="list-inline-item"><a class="p-2 text-white" href="'+element.properties.url+'" target="_blank"><svg class="icon icon-sm icon-white align-top"><use xlink:href="svg/sprite.svg#'+element.properties.icon+'"></use></svg><span class="sr-only">'+element.title+'</span></a></li>';

                        })
                        socialHtml += "</ul>";
                        socialFooter += "</ul>";
                        $("#it-socials-head").html(socialHtml);
                        $("#it-socials-head-xs").html(socialFooterXs);
                        $("#socialFooter").html(socialFooter);
                    }
                }

            }

            let contactFooter = '<strong >'+result.title+'</strong><br> '+result.properties.contact_address+' , '+result.properties.contact_location;
            let contactCF = '<strong >C.F: '+result.properties.contact_cf+'</strong>';
            $("#contactName").html(contactFooter);
            $("#contactCf").html(contactCF);
            $("#contactPec").attr('href','mailto:'+result.properties.contact_pec)
            $("#contactPec").text(result.properties.contact_pec)
            let linkFooter = result.components.link_footer.components;
            if(linkFooter.length > 0){
                var linkAreaHtml = "<h6>Link</h6><hr class='bg-white'> <div class=\"link-list-wrapper dark\"><ul class=\"footer-list link-list clearfix\">\n";
                linkFooter.forEach(function (element) {
                    linkAreaHtml += '<li><a class="list-item text-white" href="'+element.properties.url+'" target="_self" title="Vai alla pagina:'+element.title+'">'+element.title+'</a></li>';
                });
                linkAreaHtml += "</ul></div>"
            }
            $("#linkAreaFooter").html(linkAreaHtml);
        }});
