
import { StringRenderer } from "../../commons/js/string_renderer.js";

export const VistaComunicatiStampa = {
    render: function (result_api, queryArchivio) {
        if (result_api.content && result_api.content.length) {
            let objs = result_api.content;
            var content = "";
            for (var h = 0; h < objs.length; h++) {
                let news = objs[h];
                let link_news = process.env.BASE_URL + 'consiglioinforma/notizia.html?id=' + news.id;
                var stringedDate = '';
                if (news.properties.data_pubblicazione) {
                    stringedDate = StringRenderer.renderDataTestuale(news.properties.data_pubblicazione);
                }
                content += '<div class="col p-2 m-2">\
                            <p><a class="" href="' + link_news + '" target="_self"> ' + news.title + ' </a></p>\
                            <p class="medium-font-size">\
                            <span class="dark-obj-name-bold">' + stringedDate + ' </span> \
                            ' + news.properties.intestazione + '\
                            </p>\
                        </div>';
            }
            let link_archivio =  process.env.BASE_URL + 'consiglioinforma/notizie.html?' + queryArchivio;
            return '<div class="container-fluid">\
                        <hr class="border-news mb-0"/>\
                        <div class="row">\
                            <div class="col col-sm-6">\
                                <h6 class="dark-obj-name ml-2"> Comunicati stampa </h6>\
                            </div>\
                            <div class="col col-sm-6">\
                                <h6 class="dark-obj-name float-right">\
                                    <a class="dark-obj-name" href="' + link_archivio + '"> ARCHIVIO </a>\
                                </h6>\
                            </div>\
                            <div class="container row bg-title-area  ml-1 mr-2" >' + content + '</div>\
                        </div>\
                    </div>';
        }
        return '';
    }

}

export const ServizioComunicatiStampa = {
    carica: function (filtro) {
        let body = Object.assign({
            "approfondimento": "F",
            "SORT": [{
                "direction": "DESC",
                "property": "data_pubblicazione"
            },
            {
                "direction": "DESC",
                "property": "data_pubblicazione_time"
            }]
        }, filtro)
        return new Promise(function (resolve, reject) {
            $.ajax({
                method: 'POST',
                url: apibase.baseurl + "10500/?size=2",
                data: JSON.stringify(body),
                success: resolve,
                error: reject,
                contentType: 'application/json',
                dataType: 'json'
            });
        });
    }
}