export const StringRenderer = {
    renderData: function (date, separatore) {
        if (separatore == undefined) {
            separatore = ' ';
        }
        if (typeof date === 'string') {
            date = new Date(date);
        }
        let giorno = date.getDate();
        if (giorno < 10) {
            giorno = '0' + giorno;
        }
        let mese = date.getMonth() + 1;
        if (mese < 10) {
            mese = '0' + mese;
        }
        if (!(date instanceof Date) || isNaN(date.getTime())) {
            return '';
        }
        return '<span class="no-wrap">' + giorno + separatore + mese + separatore + date.getFullYear() + '</span>';
    },
    renderDataTestuale: function (date, separatore) {
        if (separatore == undefined) {
            separatore = ' ';
        }
        if (typeof date === 'string') {
            date = new Date(date);
        }
        if (!(date instanceof Date) || isNaN(date.getTime())) {
            return '';
        }
        return (date.getDate()) + separatore + (monthNames[date.getMonth()]) + separatore + date.getFullYear();
    },
    renderDataHeader: function () {
        const date = new Date();
        let ore = date.getHours();
        if (ore < 10) {
            ore = '0' + ore;
        }
        let minuti = date.getMinutes();
        if (minuti < 10) {
            minuti = '0' + minuti;
        }
        const dateString = date.getDate() + ' ' + (monthNames[date.getMonth()]).substring(0, 3) + ' ' + date.getFullYear();
        const timeString = ore + ':' + minuti;
        return dateString + ', ' + timeString;
    },
    renderValuta: function (importo, valuta) {
        const stringaValuta = valuta ? ' ' + valuta : ' ';
        if (importo && typeof importo == 'number') {
            const euro = importo.toFixed(2).split('.');
            const intero = euro[0].replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1.')
            const decimale = euro[1];
            return '<span class="no-wrap">' + intero + ',' + decimale + stringaValuta + '</span>';
        }
        return '';
    }
}
