var MegaMenuRenderer = function (crealink) {
    this.crealink = crealink;
};

(function () {

    function creaVoceMenuFlat(element) {
        let link = this.crealink(element);
        return '<a class="list-item text-white" href="' + link.url + '"><span>' + element.title.toUpperCase() + '</span></a>';
    }

    function creaSubmenuFlat(element) {
        let children = element.children;
        let submenu = '';
        var submenuItems = '';
        for (var i = 0; i < numPage; i++) {
            let finish = i * 5 + 5;
            let subLinksForCol = children.slice(i * 5, finish);
            for (var j = 0; j < subLinksForCol.length; j++) {
                let link = this.crealink(subLinksForCol[j], element)
                submenuItems += '<li><a class="list-item text-white" href="' + link.url + '"><span>' + subLinksForCol[j].title + '</span></a>';
            }
        }
        submenu = '\
                    <a class="list-item large medium right-icon text-white" href="#collapse' + element.id + '" data-toggle="collapse" aria-expanded="false" aria-controls="collapse' + element.title + '">\
                        <span>' + element.title.toUpperCase() + '</span>\
                        <svg class="icon icon-xs icon-white text-white right"><use xlink:href="svg/sprite.svg#it-expand"></use></svg>\
                    </a>\
                    <ul class="link-sublist collapse" id="collapse' + element.id + '">' + submenuItems + '</ul>';
        return submenu;
    }

    MegaMenuRenderer.prototype.render = function (result) {
        let megaMenuHtml = "";
        if (result != null && result.children != undefined) {
            let megaMenuLinks = result.children;
            for (var k = 0; k < megaMenuLinks.length; k++) {
                var element = megaMenuLinks[k];
                let children = element.children;
                if (children && children.length) {
                    let numPage = Math.ceil(children.length / 5);
                    let col_val = Math.floor(12 / numPage);
                    var multiplier = -160;
                    if (numPage > 2) {
                        multiplier = -240;
                    }
                    let dimLeft = k * multiplier;
                    let maxcontent = "";
                    if (k != (megaMenuLinks.length - 1)) {
                        maxcontent = "max-content";
                    } else {
                        maxcontent = "";
                    }
                    var pageHtml = '';
                    for (var i = 0; i < numPage; i++) {
                        let finish = i * 5 + 5;
                        let subLinksForCol = children.slice(i * 5, finish);
                        var itemHtml = ''
                        for (var j = 0; j < subLinksForCol.length; j++) {
                            let ref_type = subLinksForCol[j].properties.ref_typeid;
                            let sublink = subLinksForCol[j];
                            let link = this.crealink(sublink, element);
                            itemHtml += '<li><a class="list-item menu_cliccked" id="' + ref_type + '" href="' + link.url + '" target="' + link.target + '"><span>' + subLinksForCol[j].title + '</span></a></li>';
                        }
                        pageHtml += '<div class="col-' + col_val + '">\
                                        <div class="link-list-wrapper">\
                                            <ul class="link-list">' + itemHtml + "</ul>\
                                        </div>\
                                    </div>"
                    }
                    megaMenuHtml += '\
                        <div class="col bg-dark-blue">\
                            <nav class="navbar navbar-expand-lg has-megamenu">\
                                <div class="navbar-collapsable col-12" id="nav' + element.id + '">\
                                    <div class="overlay"></div>\
                                        <div class="close-div sr-only">\
                                            <button class="btn close-menu" type="button"><span class="it-close"></span>close</button>\
                                        </div>\
                                        <div class="menu-wrapper">\
                                            <ul class="navbar-nav">\
                                                <li class="nav-item dropdown megamenu">\
                                                    <a class="nav-link dropdown-toggle text-center" href="#" data-toggle="dropdown" aria-expanded="false">\
                                                        <span class="menu-action" >' + element.title.toUpperCase() + '</span>\
                                                    </a>\
                                                    <div class="dropdown-menu expanded ' + maxcontent + '" style="left: ' + dimLeft + 'px">\
                                                        <div class="row">' + pageHtml + '</div>\
                                                    </div>\
                                                </li>\
                                            </ul>\
                                        </div>\
                                    </div>\
                                </div>\
                            </nav>\
                        </div>';
                } else {
                    var link = this.crealink(element);
                    var itemHtml = '<a class="btn text-white" id="' + element.properties.ref_type + '" href="' + link.url + '" target="' + link.target + '"><span>' + element.title + '</span></a>';
                    megaMenuHtml += '<div class="col bg-dark-blue">' + itemHtml + '</div>';
                }
            }
        }
        return megaMenuHtml;
    }

    MegaMenuRenderer.prototype.renderMobile = function (result) {
        var megaMenuXsHtml = "";
        if (result != null && result.children != undefined) {
            let megaMenuLinks = result.children;
            for (var k = 0; k < megaMenuLinks.length; k++) {
                var element = megaMenuLinks[k];
                let children = element.children;
                if (children && children.length) {
                    megaMenuXsHtml += '<li>' + creaSubmenuFlat.bind(this)(element) + '</li>';
                } else {
                    megaMenuXsHtml += '<li>' + creaVoceMenuFlat.bind(this)(element) + '</li>';
                }
            }
        }
        return megaMenuXsHtml;
    }

    MegaMenuRenderer.prototype.renderFlat = function (result) {
        var megaMenuIE = "";
        if (result != null && result.children != undefined) {
            let megaMenuLinks = result.children;
            for (var k = 0; k < megaMenuLinks.length; k++) {
                var element = megaMenuLinks[k];
                if (element.children && element.children.length) {
                    megaMenuIE += '<div class="col text-white p-2 mt-2">' + creaSubmenuFlat.bind(this)(element) + '</div>';
                } else {
                    megaMenuIE += '<div class="col text-white p-2 mt-2">' + creaVoceMenuFlat.bind(this)(element) + '</div>';
                }
            }
        }
        return megaMenuIE;
    }


})();



export const MegaMenu = MegaMenuRenderer;