function ItemConfigurazione(data) {
    Object.assign(this, data);
}

ItemConfigurazione.prototype.getSottovista = function (id) {
    return this.sottoviste[id];
};

export const Configurazione = function (config) {
    this.defaultTemplate = config.defaultTemplate;
    this.config = config.mapping.map(function (element) {
        element.sottoviste = element.sottoviste || {};
        return new ItemConfigurazione(element);
    });
};

Configurazione.prototype.getConfigurazione = function (otype) {
    for (var config of this.config) {
        if (config.otype == otype) {
            return config;
        }
    }
    return new ItemConfigurazione({
        otype: otype,
        template: this.defaultTemplate,
        sottoviste: {}
    });
};

